"use client";
import React, { useState } from "react";
import { Button } from "../ui/button";
import { Share2Icon, XIcon, ClipboardIcon, FacebookIcon, MessageSquareIcon } from "lucide-react";

export default function ShareButton() {
  const [open, setOpen] = useState(false);
  const url = encodeURIComponent(window.location.href);
  const title = encodeURIComponent(document.title);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(window.location.href);
      alert("Link copied to clipboard!");
    } catch {
      alert("Failed to copy link.");
    }
  };

  const handleNativeShare = async () => {
    if (navigator.share) {
      try {
        await navigator.share({
          title: document.title,
          text: "Check this out!",
          url: window.location.href,
        });
        setOpen(false);
      } catch (err) {
        console.error("Native share failed:", err);
      }
    } else {
      handleCopy();
    }
  };

  return (
    <>
      <Button variant="ghost" onClick={() => setOpen(true)}>
        <Share2Icon className="w-5 h-5" />
      </Button>

      {open && (
        <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center">
          <div className="bg-white dark:bg-gray-900 rounded-xl shadow-lg p-6 w-80 relative">
            <button onClick={() => setOpen(false)} className="absolute top-2 right-2">
              <XIcon className="w-5 h-5 text-gray-500 hover:text-gray-800" />
            </button>

            <h2 className="text-lg font-semibold mb-4">Share this page</h2>

            <div className="grid grid-cols-2 gap-4">
              <a
                href={`https://api.whatsapp.com/send?text=${title}%20${url}`}
                target="_blank"
                rel="noopener noreferrer"
                className="bg-green-500 text-white rounded-lg px-4 py-2 text-center hover:bg-green-600"
              >
                WhatsApp
              </a>

              <a
                href={`https://www.facebook.com/sharer/sharer.php?u=${url}`}
                target="_blank"
                rel="noopener noreferrer"
                className="bg-blue-600 text-white rounded-lg px-4 py-2 text-center hover:bg-blue-700"
              >
                Facebook
              </a>

              <a
                href={`fb-messenger://share/?link=${url}`}
                className="bg-indigo-600 text-white rounded-lg px-4 py-2 text-center hover:bg-indigo-700"
              >
                Messenger
              </a>

              <button
                onClick={handleCopy}
                className="bg-gray-700 text-white rounded-lg px-4 py-2 hover:bg-gray-800"
              >
                Copy Link
              </button>

              <button
                onClick={handleNativeShare}
                className="col-span-2 mt-2 bg-gray-100 dark:bg-gray-800 dark:text-white rounded-lg px-4 py-2 hover:bg-gray-200 dark:hover:bg-gray-700"
              >
                Native Share
              </button>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
